  margin: 0; }
  .figure-background:before {
    background: rgba(0, 0, 0, 0.4);
    display: block;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    pointer-events: none; }
